.bg-dark{
/* IE10+ */

/* Mozilla Firefox */

/* Opera */ 
background-image: -o-linear-gradient(top, #242B4A 0%, #161A2D 100%);

/* Webkit (Safari/Chrome 10) */ 
background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, #242B4A), color-stop(100, #161A2D));

/* Webkit (Chrome 11+) */ 
background-image: -webkit-linear-gradient(top, #242B4A 0%, #161A2D 100%);

/* W3C Markup */ 
background-image: -webkit-gradient(linear, left top, left bottom, from(#242B4A), to(#161A2D)); 
background-image: linear-gradient(to bottom, #242B4A 0%, #161A2D 100%);
}


